* {
  padding: 0;
  margin: 0;
}
.fixed {
  position: sticky;
  top: 0px;
  z-index: 100;
}
.header_container {
  background-color: #1a1a1a;
  display: flex;
  gap: 10px;
  align-items: center;
  color: white;
  height: 70px;
}
.logo_container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 250px;
}
.logo_container img {
  width: 80%;
  padding-top: 5pm;
  margin-left: 20px;
  vertical-align: middle;
}
.delivery {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-weight: bold;
  padding: 5px;
}
.delivery p {
  font-size: 10px;
}
.search {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  flex: 1;
}
.search select,
.search input {
  padding: 10px;
  font-weight: bold;
  outline: 1px solid white;
}
.search select {
  border: none;
}
.search input {
  border: 1px solid white;
  width: 100%;
  margin-right: -2px;
}
.search svg {
  padding: 7px;
  background: #fcb669;
  color: #1a1a1a;
}
.search svg:hover {
  background-color: #d49644;
}
.search:focus-within {
  border: 2px solid rgb(235, 161, 173);
}
.order_container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 450px;
  gap: 10px;
}
.order_container a {
  text-decoration: none;
  color: white;
}
.order_container p {
  font-size: 10px;
}
.language {
  width: 100px;
  display: flex;
  padding: 10px;
}
.language select {
  background-color: #1a1a1a;
  border: none;
  color: white;
}
.language img {
  width: 40%;
}
.cart {
  position: relative;
}
.cart span {
  position: absolute;
  font-weight: bold;
  top: -6px;
  font-size: 20px;
  background-color: #1a1a1a;
  left: 14px;
  color: orange;
}
.order_container a:hover,
.delivery:hover,
.lower_container ul li:hover,
.logo_container a:hover {
  border: 1px solid white;
  border-radius: 3px;
}
.lower_container {
  background-color: #232f3e;
  color: white;
}
.lower_container ul {
  display: flex;
  list-style: none;
  gap: 5px;
}
.lower_container ul li {
  padding: 8px;
}
.lower_container ul li:first-child {
  display: flex;
  gap: 2px;
  align-items: center;
}
.languageDropdown {
  position: absolute;
  top: 60px; /* Adjust as needed */
  right: 95px;
  background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  width: 200px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 10;
  /* text-align: center;  */ 
}
 .languageDropdown form {
  display: flex;
  flex-direction: column;
}

.languageDropdown div {
  margin-bottom: 10px;
}

.languageDropdown input[type="radio"] {
  margin-right: 5px; 
  margin-left: 5px;/* Space between radio button and label */
}

.languageDropdown label {
  cursor: pointer;
  font-size: 16px;
  color:black;
  padding:5px;
  margin-left:5px;
}

.languageDropdown input[type="radio"]:checked + label {
  font-weight: bold; /* Highlight selected language */
}

.languageDropdown label:hover {
  background-color: #f0f0f0;
}

@media screen and (max-width: 676px) {
  .header_container {
    display: flex;
    flex-direction: column !important;
    height: auto;
  }
  .lower_container ul li:not(:first-child) {
    display: none;
  }
  .language {
    display: none;
  }
}
